import {
  Fonts,
  stripSpecialCharacters,
  styled,
  translate,
  useUserGroup,
} from '@true/core';
import React, {memo, useMemo} from 'react';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {TextAvatar} from '@policy/components/TextAvatar';
import {MapList} from '@policy/components/MapList';

export const DetailRecipient = memo(({policy}: {policy?: IDetailPolicy}) => {
  return (
    <SContainer>
      <MapList
        data={policy?.teams || []}
        renderItem={({item}) => <RecipientItem id={item} />}
      />
    </SContainer>
  );
});

const SContainer = styled.View`
  margin-bottom: 8px;
`;

const RecipientItem = memo(({id}: {id: string | number}) => {
  const team = useUserGroup(id);
  // console.log("team", team);

  const subtitle = useMemo(() => {
    if (team?.metatype == 'user')
      return `@${team?.path} . ${translate('policy.specific_user')}`;
    return `@${team?.path} . ${team?.owners?.length || 0} members`;
  }, [team]);

  return (
    <SRecipientContainer>
      <TextAvatar size={40} text={team?.name || ''} />
      <STextWrapper>
        <STitle>{stripSpecialCharacters(team?.name)}</STitle>
        <SSubTitleWr>{subtitle}</SSubTitleWr>
      </STextWrapper>
    </SRecipientContainer>
  );
});

const SRecipientContainer = styled.View`
  flex-direction: row;
  align-items: center;
  margin: 8px 0;
`;
const STextWrapper = styled.View`
  margin-left: 8px;
`;
const STitle = styled.Text`
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Regular};
`;
const SSubTitleWr = styled.Text`
  font-size: 12px;
  line-height: 16px;
  color: ${p => p.theme.neutral2};
`;
