import {interactManager, memoForwardRef} from '@true/core';
import React, {RefObject, useMemo, useState} from 'react';
import {BottomSheetModal} from '@gorhom/bottom-sheet';
import {translate} from '@true/core';
import {BottomSheetMenu} from '@policy/components/BottomSheetMenu';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {useMe} from '@true/core';
import {EReview} from '@policy/screens/PolicyDetailScreen/index';
import {
  requestPermanentlyDeletePolicy,
  requestSoftDeletePolicy,
} from '@policy/store/policies/functions';
import {useAsyncFn, useAutoToastError, useBoolean} from '@true/core';
import {ConfirmationDialog} from '@true/ui';
import {useTheme} from '@true/core';
import {Colors} from '@true/core';

interface Props {
  policy?: IDetailPolicy;
}

export const DetailBottomSheet = memoForwardRef(
  ({policy}: Props, ref: RefObject<BottomSheetModal>) => {
    if (policy?.id === undefined) return null;

    const [visible, show, hide] = useBoolean();

    const [currentState, setCurrentState] = useState({
      title: '',
      onPress: () => {},
    });

    /* -- requestFunctions------------------------------------------------------ */
    /* soft delete */
    const [{loading: softLoading, error: softError}, onSoftDelete] = useAsyncFn(
      async () =>
        await requestSoftDeletePolicy(['all', 'pending'])({
          id: policy?.id as unknown as string,
          value: -1,
        }),
      [policy],
    );

    /* archive delete */
    const [{loading: archiveLoading, error: archiveError}, onArchiveDelete] =
      useAsyncFn(
        async () =>
          await requestSoftDeletePolicy(['all', 'pending'])({
            id: policy?.id as unknown as string,
            value: 1,
          }),
        [policy],
      );

    /* permanently delete */
    const [{loading, error}, onPermanentlyDelete] = useAsyncFn(
      async () =>
        await requestPermanentlyDeletePolicy({
          id: policy?.id as unknown as string,
        }),
      [policy],
    );

    /*-------------------------------------------------------------------------*/

    const me = useMe();
    const theme = useTheme();

    const items = useMemo(() => {
      for (let i in policy?.owners) {
        const _user = policy?.owners[i as unknown as number];

        /* options while pending */
        if (
          // @ts-ignore
          me?.id == _user?.id &&
          _user?.status == EReview.pending &&
          policy?.status == EReview.pending &&
          policy?.archived == 0
        ) {
          return [
            // {
            //   title: translate('policy.see_all_linked_cases'),
            //   onPress: () => {},
            // },
            // {title: translate('policy.view_test_sessions'), onPress: () => {}},
            {
              title: translate('policy.soft_delete'),
              onPress: () => {
                setCurrentState({
                  title: translate('policy.soft_delete'),
                  onPress: () => onSoftDelete(),
                });
                interactManager(() => show());
              },
            },
            {
              title: translate('policy.permanently_delete'),
              onPress: () => {
                setCurrentState({
                  title: translate('policy.permanently_delete'),
                  onPress: () => onPermanentlyDelete(),
                });
                interactManager(() => show());
              },
            },
            {
              title: translate('policy.archived'),
              onPress: () => {
                setCurrentState({
                  title: translate('policy.archived'),
                  onPress: () => onArchiveDelete(),
                });
                interactManager(() => show());
              },
            },
            {title: translate('policy.activity_logs'), onPress: () => {}},
          ];
        }
      }

      /* options after approved */
      return [{title: translate('policy.activity_logs'), onPress: () => {}}];
    }, [policy?.owners, policy?.status]);

    useAutoToastError(softError);
    useAutoToastError(archiveError);
    useAutoToastError(error);

    return (
      <>
        <BottomSheetMenu name={'detailScreen'} ref={ref} items={items} />
        <ConfirmationDialog
          isVisible={visible}
          onCloseRequest={hide}
          title={currentState.title}
          content={translate('policy.confirm_detail_content', {
            type: currentState.title,
          })}
          buttons={[
            {text: translate("policy.close")},
            {
              text: translate("policy.confirm"),
              backgroundColor: theme.primaryColor,
              color: Colors.white,
              onPress: currentState.onPress,
              loading: softLoading || archiveLoading || loading,
            },
          ]}
        />
      </>
    );
  },
);
