import React, {memo, useCallback, useState} from 'react';
import {
  IC_APPROVED_STATUS,
  IC_PENDING_STATUS,
  IC_REJECTED_STATUS,
} from '@policy/asset';
import {Text} from 'react-native';
import {Fonts} from '@true/core';
import {getUser} from '@true/core';
import {
  BottomButton,
  UIBottomButton,
  MyButton,
  ConfirmationDialog,
} from '@true/ui';
import {styled, translate} from '@true/core';
import {Colors} from '@true/core';
import {EReview, IApprovalData} from '@policy/screens/PolicyDetailScreen/index';
import {navigateToApproveScreen} from '@policy/utils';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';
import {SharedValue} from 'react-native-reanimated';
import {Checkbox} from '@policy/components/Checkbox';
import {useTheme} from '@true/core';
import {useAsyncFn, useAutoToastError, useBoolean} from '@true/core';
import {requestAttestPolicy} from '@policy/store/policies/functions';

export const ApproveButton = memo(
  ({
    approvableData,
    id,
    animatedButtonHeight,
  }: {
    approvableData: IApprovalData;
    id: string | number;
    animatedButtonHeight?: SharedValue<number>;
  }) => {
    if (approvableData.status == -1) return null;

    const [isSelected, setIsSelected] = useState(false);
    const [visible, show, hide] = useBoolean();
    const [visibleTest, showTest, hideTest] = useBoolean();

    const theme = useTheme();

    const [{loading, error}, onCompleteAttest] = useAsyncFn(
      async () => await requestAttestPolicy({id: id as string}),
      [id],
    );

    // const onCompleteAttest = useCallback(() => {
    //   requestAttestPolicy()
    // }, []);

    useAutoToastError(error);

    return (
      <SBottomWrapper
        onLayout={e => {
          if (animatedButtonHeight) {
            animatedButtonHeight.value = e.nativeEvent.layout.height;
          }
        }}>
        <SNotiStatusWrapper
          style={
            approvableData.backgroundColor !== undefined
              ? {backgroundColor: approvableData.backgroundColor}
              : {}
          }>
          {approvableData.status == EReview.attest ? (
            <Checkbox
              keyName={''}
              onChangeValue={(key, val) => {
                setIsSelected(val);
              }}
            />
          ) : (
            <SNotiStatusIc
              style={{tintColor: approvableData.color}}
              source={
                approvableData.status === 0
                  ? IC_PENDING_STATUS
                  : approvableData.status === 1
                  ? IC_APPROVED_STATUS
                  : IC_REJECTED_STATUS
              }
            />
          )}
          <SNotiText>
            {approvableData.text}{' '}
            <Text style={{fontFamily: Fonts.Medium}}>
              {getUser(approvableData.prevUserId || '')?.name}
            </Text>
          </SNotiText>
          <SNotiNumber style={{color: approvableData.color}}>
            {approvableData.rightNum}
          </SNotiNumber>
        </SNotiStatusWrapper>
        {approvableData.status == EReview.pending ||
        approvableData.hideButton ? null : approvableData.status ==
          EReview.attest ? (
          <MyButton
            containerStyle={{
              marginHorizontal: 16,
            }}
            title={
              approvableData.testRequired
                ? translate('policy.do_test')
                : translate('policy.complete_attest')
            }
            backgroundColor={isSelected ? theme.primaryColor : theme.grey3}
            textColor={Colors.white}
            disabled={!isSelected}
            onPress={approvableData.testRequired ? showTest : show}
          />
        ) : (
          <BottomButton
            containerStyle={{
              paddingHorizontal: 16,
            }}
            leftText={translate('policy.reject')}
            rightText={translate('policy.approve')}
            leftBgColor={Colors.red1}
            rightBgColor={Colors.green1}
            textColor={Colors.white}
            leftContainerStyle={{borderRadius: 8}}
            rightContainerStyle={{borderRadius: 8}}
            onLeftPress={() => {
              navigateToApproveScreen({
                id,
                status: -1,
                title: translate('policy.reject_policy'),
              });
            }}
            onRightPress={() => {
              navigateToApproveScreen({
                id,
                status: 1,
                title: translate('policy.approve_policy'),
              });
            }}
          />
        )}
        <AnimatedBottomSpace />
        <ConfirmationDialog
          isVisible={visible}
          onCloseRequest={hide}
          title={translate('policy.complete_attest')}
          content={translate('policy.are_you_sure_to_attest_this_document')}
          buttons={[
            {text: translate('policy.close')},
            {
              text: translate('policy.confirm'),
              color: Colors.white,
              backgroundColor: theme.primaryColor,
              onPress: onCompleteAttest,
              loading,
            },
          ]}
        />
        <ConfirmationDialog
          isVisible={visibleTest}
          onCloseRequest={hideTest}
          title={translate('policy.doTest')}
          buttons={[]}
        />
      </SBottomWrapper>
    );
  },
);

const SNotiStatusWrapper = styled.View`
  flex-direction: row;
  margin-bottom: 12px;
  padding: 8px 16px;
  align-items: center;
  background-color: ${p => p.theme.grey6};
`;
const SNotiStatusIc = styled.Image`
  width: 16px;
  height: 16px;
`;
const SNotiText = styled.Text`
  font-size: 12px;
  line-height: 16px;
  color: ${p => p.theme.neutral1};
  flex: 1;
  margin-left: 4px;
`;
const SNotiNumber = styled.Text`
  font-size: 12px;
  line-height: 16px;
  margin-left: 4px;
  font-family: ${Fonts.Medium};
  color: ${p => p.theme.neutral1};
`;

const SBottomWrapper = styled.View`
  background-color: ${p => p.theme.backgroundColor};
  padding-bottom: 12px;
`;
