import React, {memo, useRef} from 'react';
import {styled, translate, useTheme} from '@true/core';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {DetailSectionWrapper} from '@policy/components/DetailSectionWrapper';
import {StyleSheet} from 'react-native';
import {Fonts} from '@true/core';
import {DocumentReview} from '@policy/screens/PolicyDetailScreen/DocumentReview';
import {DetailRecipient} from '@policy/screens/PolicyDetailScreen/DetailRecipient';
import {DetailLinkedDocument} from '@policy/screens/PolicyDetailScreen/DetailLinkedDocument';
import {DetailFollowers} from '@policy/screens/PolicyDetailScreen/DetailFollowers';
import {Button} from '@policy/components/Button';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';
import {EReview, IApprovalData} from '@policy/screens/PolicyDetailScreen/index';
import {DetailMasterVersioning} from '@policy/screens/PolicyDetailScreen/DetailMasterVersioning';
import {DetailDoctype} from '@policy/screens/PolicyDetailScreen/DetailDoctype';
import {DetailStorage} from '@policy/screens/PolicyDetailScreen/DetailStorage';
import {useMe} from '@true/core';
import {useAsyncFn} from 'react-use';
import {requestFollowPolicy} from '@policy/store/policies/functions';
import {useAutoToastError} from '@true/core';
import {UserListRef} from '@policy/components/UserList/utils';

export const OtherInformation = memo(
  (props: {
    policy?: IDetailPolicy;
    isApprovable?: EReview;
    approvableData?: IApprovalData;
  }) => {
    const theme = useTheme();

    const me = useMe();

    const [{loading, error}, onAddFollower] = useAsyncFn(
      async () =>
        await requestFollowPolicy({
          obj_id: props.policy?.id || -1,
          obj_token: props.policy?.token || '',
          obj_type: props.policy?.type || '',
          user_id: (me?.id as unknown as number) || -1,
        }),
      [me?.id, props],
    );

    useAutoToastError(error);

    const timeout = useRef<any>(null);

    return (
      <SContainer>
        {/*  document_review ---------------------*/}
        {props.policy?.owners.length ? (
          <DetailSectionWrapper
            isCollapsable={true}
            title={
              translate('policy.document_review') +
              (props.policy?.owners.length
                ? ` (${props.policy?.owners.length})`
                : null)
            }
            contentStyle={styles.wrapper}>
            <DocumentReview
              policy={props.policy}
              approvableData={props.approvableData}
            />
          </DetailSectionWrapper>
        ) : null}
        {/*  Master Version ---------------------*/}
        {props.policy?.mv_export?.name ? (
          <DetailSectionWrapper
            isCollapsable={true}
            title={translate('policy.versioning')}
            contentStyle={styles.wrapper}>
            <DetailMasterVersioning policy={props.policy} />
          </DetailSectionWrapper>
        ) : null}
        {/*  Doctypes ---------------------*/}
        <DetailSectionWrapper
          isCollapsable={true}
          title={translate('policy.doctypes')}
          contentStyle={styles.wrapper}>
          <DetailDoctype policy={props.policy} />
        </DetailSectionWrapper>
        {/*  recipients ---------------------*/}
        <DetailSectionWrapper
          isCollapsable={true}
          title={translate('policy.recipients')}
          contentStyle={styles.wrapper}>
          <DetailRecipient policy={props.policy} />
        </DetailSectionWrapper>
        {/*  Storage ---------------------*/}
        <DetailSectionWrapper
          isCollapsable={true}
          title={translate('policy.storage_info')}
          contentStyle={styles.wrapper}>
          <DetailStorage policy={props.policy} />
        </DetailSectionWrapper>
        {/*  linked documents ---------------------*/}
        {props.policy?.links.length ? (
          <DetailSectionWrapper
            isCollapsable={true}
            title={translate('policy.linked_documents')}
            contentStyle={styles.wrapper}>
            <DetailLinkedDocument policy={props.policy} />
          </DetailSectionWrapper>
        ) : null}
        {/*  Followers ---------------------*/}
        <DetailSectionWrapper
          title={
            translate('policy.followers') +
            (props.policy?.followers.length
              ? ` (${props.policy?.followers.length})`
              : '')
          }
          contentStyle={styles.wrapper}
          rightComp={
            <SFollowBtn
              title={
                props.policy?.followers.indexOf(
                  (me?.id as unknown as number) || -1,
                ) === -1
                  ? 'Follow'
                  : 'Unfollow'
              }
              textStyle={{
                fontSize: 14,
                lineHeight: 24,
                color: theme.primaryColor,
                fontFamily: Fonts.Medium,
              }}
              onPress={onAddFollower}
              loading={loading}
              timeout={timeout}
            />
          }>
          <DetailFollowers
            type={'follower'}
            ids={props.policy?.followers}
            showIcon={true}
            policy={props.policy}
          />
        </DetailSectionWrapper>
        {/*  Seen ---------------------*/}
        <DetailSectionWrapper
          isDivider={false}
          title={
            translate('policy.seens') +
            (props.policy?.seens.length
              ? ` (${props.policy?.seens.length})`
              : '')
          }
          contentStyle={styles.wrapper}>
          <DetailFollowers type={'seen'} ids={props.policy?.seens} />
        </DetailSectionWrapper>

        {props.isApprovable === -1 ? (
          <AnimatedBottomSpace
            style={{backgroundColor: theme.backgroundColor}}
          />
        ) : undefined}
      </SContainer>
    );
  },
);

const SContainer = styled.View`
  flex: 1;
`;

const SFollowBtn = styled(Button)`
  padding: 4px 0;
  margin: -4px 0;
`;
const SSeeAllBtn = styled(Button)`
  border-radius: 4px;
  padding: 4px 8px;
  margin: -4px 0;
`;

const styles = StyleSheet.create({
  html: {
    borderLeftWidth: 4,
    padding: 12,
    borderRadius: 4,
  },
  myButtonContainer: {
    justifyContent: 'flex-start',
    minHeight: undefined,
    marginLeft: 0,
    marginTop: 8,
  },
  myButtonText: {
    paddingLeft: 0,
    paddingRight: 0,
    fontSize: 14,
    lineHeight: 24,
    fontFamily: Fonts.Medium,
  },
  wrapper: {
    paddingHorizontal: 16,
  },
});
