import {Fonts} from '@true/core';
import {IC_OUTLINE_DROPDOWN} from '@policy/asset';
import {styled} from '@true/core';
import React, {memo, useCallback} from 'react';
import Animated, {
  interpolate,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
  useWorkletCallback,
  withTiming,
} from 'react-native-reanimated';
import {View, ViewStyle} from 'react-native';

interface Props {
  title: string;
  children?: any;
  isCollapsable?: boolean;
  rightComp?: JSX.Element;
  contentStyle?: ViewStyle;
  isDivider?: boolean;
}

export const DetailSectionWrapper = memo((props: Props) => {
  const {isDivider = true} = props;

  const visible = useSharedValue(1);
  const animatedRotate = useSharedValue(1);
  const animatedHeight = useSharedValue(0);
  const contentHeight = useSharedValue(0);

  const toggle = useCallback(() => {
    if (visible.value == 1) {
      visible.value = 0;
      animatedRotate.value = withTiming(0, {duration: 200});
      return;
    } else {
      visible.value = 1;
      animatedRotate.value = withTiming(1, {duration: 200});
    }
  }, [visible, animatedRotate]);

  useAnimatedReaction(
    () => ({
      _visible: visible.value,
      _contentHeight: contentHeight.value,
    }),
    (cur, prev) => {
      if (prev?._contentHeight == 0) {
        animatedHeight.value = cur._contentHeight;
        return;
      }
      if (cur._visible == 1) {
        animatedHeight.value = withTiming(cur._contentHeight, {duration: 200});
      } else {
        animatedHeight.value = withTiming(0, {duration: 200});
      }
    },
    [],
  );

  const animatedIconStyles = useAnimatedStyle(
    () => ({
      transform: [
        {
          rotate: interpolate(animatedRotate.value, [0, 1], [-90, 0]) + 'deg',
        },
      ],
    }),
    [],
  );

  const animatedContainerStyle = useAnimatedStyle(
    () => ({
      height: animatedHeight.value,
      opacity: interpolate(
        animatedHeight.value,
        [0, contentHeight.value],
        [0, 1],
      ),
    }),
    [],
  );

  const onLayout = useWorkletCallback(
    (e: any) => {
      'worklet';
      contentHeight.value = e.nativeEvent.layout.height;
    },
    [contentHeight],
  );

  return (
    <SContainer>
      <STitleWrapper disabled={!props.isCollapsable} onPress={toggle}>
        <STitle>{props.title}</STitle>
        {props.rightComp ? (
          props.rightComp
        ) : props.isCollapsable ? (
          <SIconWrapper>
            <SIcon style={animatedIconStyles} source={IC_OUTLINE_DROPDOWN} />
          </SIconWrapper>
        ) : null}
      </STitleWrapper>

      <SContent
        style={[
          {overflow: 'scroll'},
          props.isCollapsable ? animatedContainerStyle : null,
        ]}>
        <View style={props.contentStyle} onLayout={onLayout}>
          {props.children}
        </View>
      </SContent>

      {/*{isDivider && <SDivider />}*/}
    </SContainer>
  );
});

const SContainer = styled.View`
  //padding: 12px 16px;
  background-color: ${p => p.theme.backgroundColor};
  margin-top: 8px;
`;

const STitleWrapper = styled.TouchableOpacity`
  flex-direction: row;
  padding: 12px 16px;
  align-items: center;
`;

const STitle = styled.Text`
  font-size: 16px;
  line-height: 24px;
  font-family: ${Fonts.Medium};
  flex: 1;
  color: ${p => p.theme.neutral1};
`;

const SContent = styled(Animated.View)``;
const SIconWrapper = styled.View``;
const SIcon = styled(Animated.Image)`
  height: 16px;
  width: 16px;
  tint-color: ${p => p.theme.neutral1};
`;
