import { memo } from "react";

export interface IMapItem<T> {
  item: T;
  index: number;
}

interface Props<T> {
  data: T[];
  renderItem: (props: IMapItem<T>) => JSX.Element
}

export const MapList = (<T extends unknown>({ data, renderItem }: Props<T>) => {
  return data.map((item, index) => renderItem({item, index})) as unknown as JSX.Element
})
