import React, {memo, useCallback} from 'react';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {styled, translate} from '@true/core';
import {UsersAvatarRow} from '@policy/components/UserAvatarsRow';
import {IC_ADD_USER} from '@policy/asset';
import {View} from 'react-native';
import {navigateToUserPickerModal} from '@policy/utils';
import {requestFollowPolicy} from '@policy/store/policies/functions';
import {UserListRef} from '@policy/components/UserList/utils';
import {UserListScreen} from '@policy/components/UserList/UserListScreen';

export const DetailFollowers = memo(
  ({
    ids,
    showIcon,
    policy,
    type,
  }: {
    ids?: (number | string)[];
    showIcon?: boolean;
    policy?: IDetailPolicy;
    type: 'follower' | 'seen';
  }) => {
    const onPress = useCallback(() => {
      navigateToUserPickerModal({
        title: translate('policy.followers'),
        multiple: false,
        onSubmit: value => {
          requestFollowPolicy({
            obj_id: policy?.id || -1,
            obj_token: policy?.token || '',
            obj_type: policy?.type || '',
            user_id: value,
          }).then();
        },
      });
    }, []);

    return (
      <SContainer>
        <UsersAvatarRow
          maxUsers={3}
          size={32}
          ids={ids}
          onPress={() => {
            UserListRef.current?.present({
              data: ids as string[],
              title: translate(`policy.${type}s`),
              onTrashPress: async value => {
                await requestFollowPolicy({
                  obj_id: policy?.id || -1,
                  obj_token: policy?.token || '',
                  obj_type: policy?.type || '',
                  user_id: value[0] as unknown as number,
                });

                UserListRef.current?.removeItem(value[0]);
              },
              hideTrashIcon: type === 'seen',
            });
          }}
        />
        <View style={{flex: 1}} />
        {showIcon ? (
          <STouch onPress={onPress}>
            <SIcon source={IC_ADD_USER} />
          </STouch>
        ) : null}
        <UserListScreen ref={UserListRef} name={'add_follower'} />
      </SContainer>
    );
  },
);

const SContainer = styled.View`
  padding-bottom: 16px;
  flex-direction: row;
  align-items: center;
`;

const STouch = styled.TouchableOpacity.attrs(p => ({
  hitSlop: {
    top: 8,
    bottom: 8,
    left: 8,
    right: 8,
  },
}))``;

const SIcon = styled.Image`
  height: 24px;
  width: 24px;
  tint-color: ${p => p.theme.neutral1};
`;
