import { memo, useCallback } from "react";
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {styled} from '@true/core';
import {usePolicies} from '@policy/store/policies';
import {Fonts, IC_DOCUMENT} from '@true/core';
import { IC_OUTLINED_DOCUMENT, IC_POLICY_DOCUMENT } from "@policy/asset";
import { FlatList } from "react-native";
import { MapList } from '@policy/components/MapList';
import { navigateToPolicyDetail } from "@policy/utils";

export const DetailLinkedDocument = memo(
  ({policy}: {policy?: IDetailPolicy}) => {
    return (
      <SContainer>
        <MapList
          data={policy?.links || []}
          renderItem={({item}) => <LinkedDocItem id={item} />}
        />
      </SContainer>
    );
  },
);

const SContainer = styled.View`
  padding-bottom: 8px;
`;

const LinkedDocItem = memo(({id}: {id: string | number}) => {
  const policy = usePolicies(id);

  const onPress = useCallback(() => {
    navigateToPolicyDetail({id})
  }, [id])

  return (
    <SLinkedContainer onPress={onPress}>
      <SIconWrapper>
        <SIcon source={IC_OUTLINED_DOCUMENT} />
      </SIconWrapper>
      <STextWrapper>
        <STitle>{policy?.name}</STitle>
        <SSubTitle>{policy?.scode}</SSubTitle>
      </STextWrapper>
    </SLinkedContainer>
  );
});

const SLinkedContainer = styled.TouchableOpacity`
  flex-direction: row;
  padding: 8px 0;
`;

const SIconWrapper = styled.View`
  justify-content: center;
  max-height: 36px;
`;

const SIcon = styled.Image`
  margin-right: 8px;
  width: 24px;
  height: 24px;
`;
const STextWrapper = styled.View`
  flex: 1;
`;
const STitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Medium};
`;
const SSubTitle = styled.Text`
  font-size: 12px;
  line-height: 16px;
  color: ${p => p.theme.neutral2};
`;
