import { styled, useAsyncFn, useAutoToastError } from "@true/core";
import React, {memo, useCallback, useEffect, useState} from 'react';
import {useUserList} from './provider';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {TouchableOpacity} from 'react-native-gesture-handler';
import {useUser} from '@true/core';
import {ProfileLightBox, UIButton, UserAvatar} from '@true/ui';
import {Fonts, IC_TRASH_OUTLINE} from '@true/core';
import {useBoolean} from '@true/core';

const AnimatedTouch = Animated.createAnimatedComponent(TouchableOpacity);

export const UserItem = memo(({item}: {item: string}) => {
  const {
    onPressItem,
    selectedIds,
    _onTrashPress,
    rawData,
    data,
    hideTrashIcon,
  } = useUserList();
  const [visible, show, hide] = useBoolean(false);

  // const [loading, setLoading] = useState(false);

  // useEffect(() => {
  //   if (rawData.length !== data.length) {
  //     setLoading(false);
  //   }
  // }, [rawData.length]);

  const user = useUser(item);

  const _onPress = useCallback(() => {
    onPressItem(item);
  }, [item]);

  const [{loading, error},__onTrashPress] = useAsyncFn(async () => {
    // setLoading(true);
    await _onTrashPress?.(item);
  }, [item]);

  useAutoToastError(error)

  // const animatedStyle = useAnimatedStyle(
  //   () => ({
  //     backgroundColor:
  //       selectedIds.value.indexOf(item) !== -1 ? 'yellow' : 'white',
  //   }),
  //   [],
  // );

  return (
    <Wrapper onPress={_onPress} onLongPress={show}>
      <UserAvatar size={40} userKey={item} />
      <NameSection>
        <Name numberOfLines={1}>{user?.name || 'Deactivated account'}</Name>
        {user?.title ? (
          <Subtitle numberOfLines={1}>{user.title}</Subtitle>
        ) : null}
      </NameSection>
      {!hideTrashIcon ? (
        <UIButton
          onPress={__onTrashPress}
          source={IC_TRASH_OUTLINE}
          loading={loading}
          hideContentWhileLoading={true}
        />
      ) : null}

      <ProfileLightBox
        userKey={item}
        isVisible={visible}
        onCloseRequest={hide}
      />
    </Wrapper>
  );
});

const SContainer = styled(AnimatedTouch)`
  padding: 12px 16px;
`;

const SText = styled.Text`
  color: ${p => p.theme.neutral1};
`;

export const MEMBER_ROW_HEIGHT = 56;

const Wrapper = styled.TouchableOpacity`
  height: ${MEMBER_ROW_HEIGHT}px;
  flex-direction: row;
  align-items: center;
  padding: 8px 16px;
  width: 100%;
  border-color: ${p => p.theme.neutral5};
`;

const NameSection = styled.View`
  margin-left: 16px;
  flex: 1;
`;

const Name = styled.Text`
  font-size: 16px;
  font-family: ${Fonts.Bold};
  line-height: 20px;
  color: ${p => p.theme.neutral1};
`;

const Subtitle = styled.Text`
  font-size: 14px;
  line-height: 18px;
  color: ${p => p.theme.grey3};
`;
