import { styled } from "@true/core";
import { memo, useMemo } from "react";
import { IPolicy } from "@policy/store/policies/types";
import { AttachmentPreview } from "@policy/screens/PolicyDetailScreen/AttachmentPreview";
import { IDetailPolicy } from "@policy/store/detailPolicy/types";

interface Props {
  policy?: IDetailPolicy;
}

export const DetailAttachments = memo((props: Props) => {
  const files = useMemo(() => props.policy?.files, [props.policy?.files]);
  return (
    <SContainer>
      <>{files?.map((file, index) => (
        <AttachmentPreview file={file} isFirst={index == 0} />
      ))}</>

    </SContainer>
  );
});

const SContainer = styled.View``;
