import {useCallback, useMemo, useRef, useState} from 'react';
import constate from 'constate';
import {UserListScreenProps} from '@policy/components/UserList/UserListScreen';
import {useSharedValue} from 'react-native-reanimated';
import {goBack} from '@true/core';

const _provider = (props: UserListScreenProps) => {
  const {data, title, onSave, multiple, rightActionText, onTrashPress, hideTrashIcon} = props;

  const [rawData, setRawData] = useState(data);

  const selectedIds = useSharedValue<string[]>([]);

  const onPressItem = useCallback(
    (id: string) => {
      const prevIds = [...selectedIds.value];

      if (multiple) {
        if (selectedIds.value.indexOf(id) === -1) {
          return (selectedIds.value = [...prevIds, id]);
        }
        return (selectedIds.value = prevIds.filter(_id => _id != id));
      }

      onSave?.([id]);
    },
    [selectedIds, onSave],
  );

  const _onTrashPress = useCallback(
    async (id: string) => {
      await onTrashPress?.([id]);
    },
    [onTrashPress],
  );

  return useMemo(
    () => ({
      data,
      hideTrashIcon,
      rawData,
      setRawData,
      selectedIds,
      onPressItem,
      _onTrashPress,
    }),
    [selectedIds, _onTrashPress, onPressItem, rawData, setRawData, data, hideTrashIcon],
  );
};

export const [UserListProvider, useUserList] = constate(_provider, val => val);
