import React, { RefObject, useCallback, useImperativeHandle } from "react";
import { BottomMenuHeader, MyBottomSheet } from "@true/ui";
import { UserListProvider, useUserList } from "./provider";
import { UserList } from "@policy/components/UserList/index";
import { memoForwardRef } from "@true/core";
import { BottomSheetModal, BottomSheetView } from "@gorhom/bottom-sheet";
import { UserListRef } from "@policy/components/UserList/utils";

export interface UserListScreenProps {
  data: string[];
  title: string;
  onSave?: (value: string[]) => void;
  onTrashPress?: (value: string[]) => Promise<void>;
  multiple?: boolean;
  rightActionText?: string;
  hideTrashIcon?: boolean;
}

export interface UserListScreen extends BottomSheetModal {
  present: (data: UserListScreenProps) => void;
  removeItem: (item: string) => void;
}

const _UserListScreen = memoForwardRef(
  (props: UserListScreenProps, ref: RefObject<UserListScreen>) => {
    const {data, title, onSave, multiple, rightActionText} = props;

    const {selectedIds, rawData, setRawData} = useUserList();
    //
    // console.log(selectedIds);
    //
    const _onSave = useCallback(() => {
      onSave?.(selectedIds.value);
    }, []);

    const removeItem = useCallback((id: string) => {
      setRawData(prev => [...prev].filter(_id => _id !== id));
    }, []);

    useImperativeHandle(
      ref,
      () => {
        return {
          ...ref.current,
          removeItem,
        } as any;
      },
      [ref],
    );

    return (
      <>
        <BottomSheetView>
          <BottomMenuHeader
            title={title}
            onClose={UserListRef.current?.dismiss}
            // rightComponent={
            //   multiple ? (
            //     <HeaderActionText
            //       onPress={_onSave}
            //       text={rightActionText || translate('save')}
            //     />
            //   ) : null
            // }
          />
        </BottomSheetView>
        <UserList data={rawData} isBottomSheet={true} />
      </>
    );
  },
);

export const UserListScreen = memoForwardRef(
  (props: {name: string}, ref: RefObject<BottomSheetModal>) => {
    return (
      <MyBottomSheet ref={ref} name={props.name} snapPoints={['100%']}>
        {_props => (
          <UserListProvider {..._props.data}>
            <_UserListScreen ref={ref} {..._props.data} />
          </UserListProvider>
        )}
      </MyBottomSheet>
    );
  },
);
