import {memoForwardRef} from '@true/core';
import {UIFlashList} from '@true/ui';
import React, {useCallback} from 'react';
import {UserItem} from '@policy/components/UserList/UserItem';

export const UserList = memoForwardRef(
  ({data, isBottomSheet}: {data: string[]; isBottomSheet?: boolean}) => {
    const renderItem = useCallback(({item}: {item: string}) => {
      return <UserItem item={item} />;
    }, []);

    return (
      <UIFlashList
        data={data}
        renderItem={renderItem}
        estimatedItemSize={50}
        isBottomSheet={isBottomSheet}
      />
    );
  },
);
