import {Fonts, stripSpecialCharacters, styled, translate} from '@true/core';
import React, {memo, RefObject} from 'react';
import {DetailSectionWrapper} from '@policy/components/DetailSectionWrapper';
import {StyleSheet} from 'react-native';
import {DetailAttachments} from '@policy/screens/PolicyDetailScreen/DetailAttachments';
import {GeneralDetails} from '@policy/screens/PolicyDetailScreen/GeneralDetails';
import {DetailDiscussions} from '@policy/screens/PolicyDetailScreen/DetailDiscussions';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import Animated from 'react-native-reanimated';
import {EReview} from '@policy/screens/PolicyDetailScreen/index';
import {DetailPolicyNote} from '@policy/screens/PolicyDetailScreen/DetailPolicyNote';
import {PreviewCommentList} from '@true/ui';
import {AnimatedCollapseContent} from '@policy/components/Text/AnimatedCollapseContent';

interface Props {
  policy?: IDetailPolicy;
  isApprovable?: EReview;
  listRef?: RefObject<PreviewCommentList>;
}

export const MainInformation = memo((props: Props) => {
  return (
    <SContainer>
      {/*  Overview ---------------------*/}
      {props.policy?.content ? (
        <DetailSectionWrapper
          title={translate('policy.note')}
          contentStyle={styles.wrapper}>
          <AnimatedCollapseContent
            content={stripSpecialCharacters(props.policy?.content || '')}
          />
        </DetailSectionWrapper>
      ) : null}
      {/*Attachments ---------------------*/}
      {props.policy?.files?.length ? (
        <DetailSectionWrapper
          title={translate('policy.attachments')}
          contentStyle={styles.wrapper}>
          <DetailAttachments policy={props.policy} />
        </DetailSectionWrapper>
      ) : null}
      {/*  Note ---------------------*/}
      {props.policy?.note ? (
        <DetailSectionWrapper
          title={translate('policy.note')}
          contentStyle={styles.wrapper}>
          <DetailPolicyNote policy={props.policy} />
        </DetailSectionWrapper>
      ) : null}
      {/*/!*  General details ---------------------*!/*/}
      <DetailSectionWrapper
        isCollapsable={true}
        title={translate('policy.general_detail')}
        contentStyle={styles.wrapper}>
        <GeneralDetails policy={props.policy} />
      </DetailSectionWrapper>
      {/*  Discussions ---------------------*/}
      <DetailSectionWrapper
        isDivider={false}
        title={translate('policy.comments')}>
        <DetailDiscussions
          listRef={props.listRef}
          isApprovable={props.isApprovable}
          policy={props.policy}
        />
      </DetailSectionWrapper>
    </SContainer>
  );
});

const SContainer = styled(Animated.View)`
  flex: 1;
`;

const styles = StyleSheet.create({
  html: {
    borderLeftWidth: 4,
    padding: 12,
    borderRadius: 4,
  },
  myButtonContainer: {
    justifyContent: 'flex-start',
    minHeight: undefined,
    marginLeft: 0,
    marginTop: 8,
  },
  myButtonText: {
    paddingLeft: 0,
    paddingRight: 0,
    fontSize: 14,
    lineHeight: 24,
    fontFamily: Fonts.Medium,
  },
  wrapper: {
    paddingHorizontal: 16,
    paddingBottom: 16,
  },
});
