import React, {memo} from 'react';
import {
  MyBottomSheet,
  useAnimatedSnapPoints,
  // BottomSheetView,
} from '@true/ui';
import {BottomSheetFlatList, BottomSheetView} from '@gorhom/bottom-sheet';
import {MenuItem} from '@policy/components/MenuItem';
import {memoForwardRef} from '@true/core';
import {AnimatedBottomSpace} from '@policy/components/AnimatedBottomSpace';

interface IMenuItem {
  title: string;
  source?: any;
  onPress?: () => void;
}

export const BottomSheetMenu = memoForwardRef(
  ({name, items}: {items: IMenuItem[]; name: string}, ref: any) => {
    const {animatedSnapPoints, onViewLayout, onListLayout} = useAnimatedSnapPoints();

    return (
      <MyBottomSheet ref={ref} name={name} snapPoints={animatedSnapPoints}>
        <BottomSheetView onLayout={onViewLayout('body')}>
          <BottomSheetFlatList
            data={items}
            renderItem={({item}) => {
              return <MenuItem
                source={item.source}
                title={item.title}
                onPress={item.onPress}
              />
            }}
            onLayout={onListLayout}
          />
          <AnimatedBottomSpace />
        </BottomSheetView>
      </MyBottomSheet>
    );
  },
);
